.btn-next,
.btn-prev {
  background-color: var(--white-color);
  border: none;
  position: absolute;
  margin-top: -185px;
}

.btn-prev {
  left: 15px;
}

.btn-next {
  right: 15px;
}

/* scroll view */
.main-view {
  margin-top: 30px;
  width: 100%;
  height: auto;
  overflow: hidden;
}

.scroll-view {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  transition: all 0.6s ease-in;
  height: 100%;
  min-width: calc(605px * 8);
  margin-bottom: 15px;
}

.scroll-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: auto;
  width: 580px;
  margin: 20px;
}

.scroll-item__img {
  width: 213px;
  margin-right: 20px;
}

.scroll-item__info > h3 {
  margin-top: 20px;
  font-size: 18px;
  font-weight: 600;
  color: var(--tertiary-color);
}

.scroll-item__info > h4 {
  font-size: 12px;
  font-weight: 400;
}

.scroll-item__info > p {
  padding-right: 10px;
}

.selector {
  list-style-type: none;
  display: flex;
  justify-content: center;
}

.selector-item {
  margin: 0 10px;
  display: flex;
  align-items: center;
}

.selector-item > button {
  border-radius: 50%;
  height: 15px;
  width: 15px;
  border: 1px solid var(--primary-color);
  background-color: var(--white-color);
  cursor: pointer;
}

.active > button {
  height: 18px;
  width: 18px;
  background-color: var(--primary-color);
}
