.jumbotron {
  background-color: var(--grey-color);
  width: 100%;
  height: 555px;
  overflow: hidden;
}

.jumbotron .container {
  display: flex;
  height: 100%;
}

.profile-info {
  align-self: center;
  width: 55%;
}
.profile-info > h1 {
  font-size: 45px;
  color: var(--primary-color);
}
.profile-info > h3 {
  font-size: 36px;
  font-weight: 300;
  line-height: 36px;
}
.profile-info > p {
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 20px;
}

.profile-image {
  align-content: flex-end;
  width: 45%;
  object-fit: cover;
  object-position: center;
}

.profile-image > img {
  top: -150px;
  position: relative;
  width: 100%;
  height: auto;
}
