.nav {
  background-color: var(--white-color);
  height: 70px;
  display: flex;
  padding: 0 20px;
  justify-content: space-between;
}

.nav-logo,
.nav-mobile {
  align-self: center;
  cursor: pointer;
  display: block;
}

.nav-mobile {
  padding: 20px 0;
}

.nav-menu,
.nav-item {
  list-style-type: none;
}

.burger,
.close {
  transition: 0.2s ease-in;
}

.open {
  right: 0 !important;
  transition: ease-in 0.2s !important;
}

.nav-menu {
  position: fixed;
  top: 70px;
  background-color: var(--white-color);
  height: 100%;
  right: -250px;
  min-width: 250px;
  transition: ease-in 0.2s !important;
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.25);
}

.nav-item {
  color: var(--tertiary-color);
}

.nav-item .active {
  background-color: var(--tertiary-color);
  color: var(--white-color);
}

.nav-link {
  line-height: 40px;
  padding: 15px 25px;
  color: var(--tertiary-color);
  text-decoration: none;
  display: block;
  font-weight: 500;
}

.nav-item:hover {
  background-color: var(--tertiary-color);
  transition: 0.4s ease-in;
}
.nav-item:hover a {
  color: var(--white-color);
  transition: 0.4s ease-in;
}

@media screen and (min-width: 954px) {
  .nav {
    padding: 0 90px;
  }

  .nav-mobile {
    display: none;
  }

  .nav-menu {
    top: 0;
    position: unset;
    margin: unset;
    right: unset;
    box-shadow: unset;
  }

  .nav-item {
    min-width: 0;
    display: inline-block;
  }

  .nav-link {
    display: inline-block;
  }
}
