.tools {
  background-color: var(--grey-color);
  padding: 50px 0;
}

.tools-item {
  display: flex;
  align-items: center;
  justify-content: center;
  align-content: center;
  width: 100%;
  margin-top: 40px;
}

.tools-item ul {
  width: 100%;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.tools-item ul li {
  margin: 20px 15px;
}
