footer {
  background-color: var(--primary-color);
  padding: 50px 0;
  color: var(--white-color);
}

footer .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-left {
  text-align: left;
}
.footer-left a {
  color: var(--white-color);
}

.footer-right {
  text-align: right;
  align-self: flex-end;
}
