.spec {
  display: flex;
  gap: 20px;
}

.spec-image {
  width: 40%;
  padding: 10px;
  padding-top: 25px;
}
.spec-image img {
  width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center;
}

.spec-items {
  width: 60%;
}

.spec-card {
  width: 45%;
  height: 265px;
  margin: 10px;
  float: left;
}

.card {
  padding: 20px;
  border: 1px solid var(--primary-color);
  text-align: center;
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.25);
}

.card-title {
  font-size: 28px;
  color: var(--tertiary-text);
  font-weight: 500;
}

.card-description {
  font-size: 14px;
}
