.project-items {
  display: inline-flex;
  flex-wrap: wrap;
}

.project-card {
  width: 50%;
  padding: 15px;
}

.project-card > img {
  width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center;
}

.project-foot {
  display: flex;
  padding: 10px 5px;
  justify-content: space-between;
}

.project-link li {
  display: inline-block;
  list-style-type: none;
  padding: 0 10px;
}

.project-link > li a {
  text-decoration: none;
  color: var(--text-dark);
}
